<template>
	<div class="newsContent">
        <div class="unit" v-for="(item,index) in newsData" :key=index @click="chooseNews(item)">
            <div class="news_title">{{item.title}}</div>
            <div class="news_content">
                <img  class="unit_image" :src="item.cover" alt="">
                <div class="news_content_right">
                    {{item.content.replace(/<[^>]+>/g, '').substr(0, 50) + '...'}}
                </div>   
            </div>
        </div>
    </div>
</template>

<style scoped lang="less">
    .newsContent {
        width: 100%;
        height: auto;
    }
    .unit {
        width:90%;
        margin:0.2rem auto;
        height: 4rem;
        border-radius:0.3rem;
        background-color: #eaeaea;
        .news_title{
            width:100%;
            height: auto;
            white-space:nowrap;
            font-size: 0.4rem;
            overflow:hidden;
            text-overflow:ellipsis;
        }
        .news_content{
            display: flex;
            margin-top:0.2rem;
            justify-content: space-around;
            img{
                width:45%;
                height:3rem;
                border-radius: 0.4rem;
            }
        }
        .news_content_right {
            width: 45%;
            height: 3rem;
            overflow: hidden;
            text-overflow: ellipsis;
            text-indent: 0.6rem;
        }
    }
    

</style>

<script>
import {getNewsList} from '../../assets/api/index.js'

export default{
    data(){
        return{
            newsData:[]
        }

    },
    methods:{
        async getNews() {
            let newsList = await getNewsList();
            console.log(newsList.data)
            this.newsData = newsList.data;
        },
        chooseNews(item){
            this.$router.push("/zhishiku/newsDetail");
            this.$store.commit("changeNewsTitle",item.title);
            // console.log(this.$store.state.newsTitle)
        }
    },
    created(){
        this.getNews();
    }
}
</script>


