<template>
  <div class="page">
    <div class="subject_title">
      {{examTitle}}
    </div>
    <div class="quiz-box" v-for="(items, index) in examList" :key="index" v-show="index===curIndex">
      <div class="quiz">
        <p class="quiz-title">{{ index + 1 }}. {{ items.content }}</p>
        <ul class="quiz-options">
          <!-- <li v-for="(item, index) in items.options" :key=index :class="['option', answer_state_list[current_index][index]]"> -->
          <li v-for="(items, index) in items.options" :key="index" @click="checkAnswer(items,index)" :class="index==optionCurIndex?isTrueAnswer:''">
            <span class="option-index">{{ changeToLetter(index) }}</span>
            <span class="option-content">{{ items }}</span>
          </li>
        </ul>
      </div>
      <div class="btn-box">
        <button  class="btn" @click="preExcercise(curIndex)">
          
        </button>
        <button class="btn" @click="nextExcercise(curIndex)">
          
        </button>
      </div>
      <div class="explain" v-show=" getExplain == true">
        <p><span class="title"></span>{{ items.reason }}</p>
      </div>
    </div>
    <!-- nav -->
    <div class="footer">
      <div class="btn"></div>
      <div class="btn" @click="getAnswer()"></div>
      <div class="btn"></div>
    </div>
  </div>
</template>


<script>
      export default {
          data(){
              return{
                  curIndex:0,
                  examList: this.$store.state.quizList.data,
                  examTitle: this.$store.state.examName,
                  getExplain:false,
                  isTrueAnswer:'',
                  optionCurIndex:''
              }
          },
          methods:{
            changeToLetter(index) {
              return String.fromCharCode(index + 65);
            },
            nextExcercise(curIndex) {
              if(curIndex<this.examList.length-1){
                this.curIndex++;
                // console.log('0k!')
                this.getExplain = false;
              }
              this.optionCurIndex = -1;
              // console.log("")
            },
            preExcercise(curIndex) {
              if(curIndex>0){
                this.curIndex--;
                this.getExplain = false;
              }
              this.optionCurIndex = -1;  
            },
            getAnswer() {
              this.getExplain = true;
            },
            goHome(){
              this.$router.push("/");
            },
            checkAnswer(items,index){
              // console.log(this.$store.state.quizList.data[this.curIndex].trueOption[0]==index+1);
              if(this.$store.state.quizList.data[this.curIndex].trueOption[0]==(index)){
                 this.isTrueAnswer = 'answer-right';
                 this.optionCurIndex = index;
              }else{
                this.isTrueAnswer = 'answer-error';
                this.optionCurIndex = index;
              }
            }
          },
          created(){
            console.log(this.$store.state.examName);
            console.log(this.examList);
          },
      }

</script>

