<template>
    <div class="quanzhong">
        <div class="indexName">
           <div class="fushi" :class="{active:index==='fushi'}"  @click="enterFushi()"></div>
           <div class="disanfang" :class="{active:index==='disanfang'}" @click="enterThird()"></div>
           <div class="dizai" :class="{active:index==='dizai'}" @click="enterDizai()"></div>
           <div class="zhizaoquexian" :class="{active:index==='zhizaoquexian'}" @click="enterZhizao()"></div>
        </div>
        <div class="content">
            <router-view></router-view>
        </div>
    </div>
</template>

<style lang="less" scoped>
    .indexName{
        display:flex;
        justify-content: space-around;
        font-size:0.3rem;
        align-items: center;
    }
    .fushi.active{
        color:red;
        font-weight:bold;
        font-size:0.4rem;
    }
    .disanfang.active{
        color:red;
        font-weight:bold;
        font-size:0.4rem;
    }
    .dizai.active{
        color:red;
        font-weight:bold;
        font-size:0.4rem;
    }
    .zhizaoquexian.active{
        color:red;
        font-weight:bold;
        font-size:0.4rem;
    }
    .content{
        margin-top: 0.3rem;
    }
</style>

<script>
export default {
    data(){
        return{
            index:"fushi",
        }
    },
    methods:{
        enterFushi(){
            this.$router.push('/zhishiku/quanzhong/fushi');
            this.index= "fushi";
        },
        enterThird(){
            this.index = "disanfang";
            this.$router.push('/zhishiku/quanzhong/disanfang');
            console.log(this.index);
        },
        enterDizai(){
            this.index="dizai";
            this.$router.push('/zhishiku/quanzhong/dizai');
        },
        enterZhizao(){
            this.index = "zhizaoquexian";
            this.$router.push('/zhishiku/quanzhong/zhizao');
        }
    }
}
</script>